.container {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  padding-top: 0;
  /* background-color: #000; */
}


.heading {
  font-weight: 500;
  font-size: 2.1rem;
}

.toolbar {
  width: 100%;
  display: flex;
  /* background-color:; */
  gap: 40px;
  justify-content: space-between;
  align-items: center;
}

.colors {
  display: flex;
  gap: 20px;
  padding: 0 30px;
}

.colors .color {
  height: 36px;
  width: 36px;
  border-radius: 50%;
  background-color: #000000;
}
.colors .active {
  border: 2px solid #000;
}

.toolbar button {
  padding: 8px 16px;
  border-radius: 5px;
  background-color: #06035d;
  color: #fff;
  font-weight: bold;
  outline: none;
  border: none;
  font-size: 1rem;
  letter-spacing: 1px;
  display: flex;
  gap: 5px;
  align-items: center;
  cursor: pointer;
}

.toolbar button svg {
  height: 20px;
  width: 20px;
}

.main {
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 100%;
}

.toolbar {
  width: 100%;
  display: flex;
  /* background-color:#06035d; */
  /* gap: 10px; */
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 900px) {
  .toolbar {
    width: 20px;
    /* background-color: #06035d; */
    display: flex;
    /* background-color:#06035d; */
    /* gap: 10px; */
    /* justify-content: center; */
    /* margin-right:50px; */
    align-items: center;
  }
  .colors .color {
    height: 26px;
    width: 26px;
    border-radius: 50%;
    
  }
  .toolbar button {
    padding: 8px 16px;
    border-radius: 5px;
    background-color: #06035d;
    color: #fff;
    font-weight: bold;
    outline: none;
    border: none;
    font-size: 1rem;
    letter-spacing: 1px;
    display: flex;
    gap: 5px;
    align-items: center;
    cursor: pointer;
    margin-left: -30px;
  }
  .colors {
    display: flex;
    gap: 2px;
    padding: 0 30px;
  }
  

}